.forecast-container {
    width: 660px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    color: $secondary-font-color;
    box-sizing: border-box;
    border-radius: 8px;
    background-color: $weather-container-background;
    padding-bottom: 50px;
    box-shadow: 0px 4px 4px rgba(4, 5, 73, 0.25), 14px 14px 20px rgba(5, 6, 114, 0.2);
    margin-right: 1em;
}

.forecast-container__details {
    width: 100%;
    padding: 50px;
    box-sizing: border-box;
}

.forecast-container__card {
    width: 100%;
    padding: 0px 50px;
    box-sizing: border-box;

    & .card-container {
        width: 100%;
   }
}

@media screen and (max-device-width: 1280px) {
    .forecast-container {
        margin-right: 0em;
        margin-bottom: 1em;
    }
}

@media screen and (max-device-width: 660px) {
    .forecast-container {
        width: 550px;
    }
}

@media screen and (max-device-width: 600px) {
   .forecast-container__card {
        padding: 0px 25px;
    }

    .forecast-container {
        width: 300px;
    }

    .forecast-container__details {
        padding: 25px;
    }
}